* {
	font-family: 'Roboto', sans-serif; }
header {
	position: relative; }
nav {
	width: 100%;
	height: 50px;
	background: rgba(33, 33, 33, 1); }
.menu {
	display: flex;
	justify-content: space-between;
	padding: 13px 70px;
	margin: 0;
	list-style-type: none;
	a {
		text-decoration: none; }
	.menu_link {
		font-size: 15px;
		font-weight: 300;
		font-style: normal;
		text-align: left;
		color: #FFFFFF; }
	.menu_item {
		border-right: 2px dotted black;
		padding-right: 28px; } }
.subheader {
	position: absolute;
	width: 100%;
	height: 87px;
	background: rgba(0, 0, 0, .6);
	.subheader_logo {
		display: block;
		max-width: 100%;
		margin-top: 16px; } }
.subheader_text {
	margin-top: 6px;
	opacity: 0.50;
	font-size: 13px;
	font-weight: 100;
	font-style: normal;
	text-align: left;
	color: #FFFFFF; }
.subheader_call {
	margin-top: 19px;
	font-size: 12px;
	font-weight: 300;
	font-style: normal;
	color: #FFFFFF; }
.subheader_phone {
	margin-top: 8px;
	font-size: 22px;
	font-weight: 400;
	font-style: normal;
	text-align: center;
	color: #FFFFFF;
	text-decoration: none; }
.subheader_btn {
	margin-top: 21px;
	width: 172px;
	height: 46px;
	box-shadow: inset 0px -4px 0px rgba(0, 0, 0, 0.12);
	background-image: linear-gradient(0deg, #1eacc7 0%, #1eacc7 0%, #4ce2ff 100%);
	border: none;
	border-radius: 4px;
	font-size: 14px;
	text-transform: uppercase;
	color: #ffffff;
	font-weight: 400;
	text-align: center;
	filter: drop-shadow(0.707px 0.707px 0px rgba(0, 0, 0, 0.21)); }
.promo {
	min-height: 800px;
	background: url(../img/backgroundfirst.png) center center /cover no-repeat;
	padding: 150px 0 135px 0;
	.promo_header {
		font-size: 60px;
		font-weight: 700;
		color: #FFFFFF;
		text-transform: uppercase;
		text-align: center; }
	.promo_subheader {
		font-size: 35px;
		font-weight: 600;
		font-style: normal;
		text-align: center;
		color: #FFFFFF; }
	.promo_descr {
		margin-top: 23px;
		font-size: 18px;
		font-weight: 400;
		font-style: normal;
		text-align: center;
		color: #FFFFFF; }
	.promo_btn {
		display: block;
		margin: 118px auto 0 auto;
		width: 259px;
		height: 63px;
		box-shadow: inset 0px -4px 0px rgba(0, 0, 0, 0.12);
		background-image: linear-gradient(0deg, #1eacc7 0%, #1eacc7 0%, #4ce2ff 100%);
		border: none;
		border-radius: 4px;
		font-size: 18px;
		line-height: 68px;
		text-transform: uppercase;
		color: #ffffff;
		font-weight: 400;
		font-family: "Roboto";
		text-align: center;
		filter: drop-shadow(0.707px 0.707px 0px rgba(0, 0, 0, 0.21)); } }
.advantage {
	.advantage_btn {
		margin: 69px auto 0 auto;
		width: 115px;
		height: 27px;
		background: rgba(0, 0, 0, 1);
		font-size: 14px;
		font-weight: 100;
		font-style: normal;
		text-align: center;
		color: #FFFFFF;
		line-height: 30px; } }
.advantage_logo {
	padding-top: 37px;
	height: 54px;
	font-size: 55px;
	line-height: 48px;
	text-transform: uppercase;
	color: #222222;
	font-weight: 700;
	text-align: center; }
.advantage_block {
	display: flex;
	justify-content: center;
	align-items: center;
	margin-top: 70px; }
.advantage_text {
	color: #202020;
	font-size: 15px;
	font-weight: 300;
	line-height: 21px; }
.advantage_img {
	display: flex;
	justify-content: center;
	align-items: center;
	margin-right: 26px;
	width: 113px;
	height: 113px;
	float: left; }
.advantage_descr {
	width: 343px; }
.advantage_subtitle {
	line-height: 21px;
	color: #1eacc7;
	font-size: 17px;
	font-weight: 400; }
